<template>
    <div>
        <div ref="map"></div>
    </div>
</template>

<script>
    import {Scene} from '@antv/l7'
    import { CountryLayer } from '@antv/l7-district';
    import { GaodeMap } from '@antv/l7-maps';
    import axios from 'axios'

    export default {
        name: "EpidemicMap",
        data(){
            return{
                scene:null,
                el:null,
                layer:null,
                ProvinceData:[]
            }
        },
        created() {
            axios.get('http://127.0.0.1:1024/getCovid19Data').then(res => {
                this.ProvinceData = res.data.data.provinceArray
            })
            this.$nextTick(()=>{
                this.el = this.$refs.map
                this.scene = new Scene({
                    id: this.el,
                    map: new GaodeMap({
                        center: [ 116.2825, 39.9 ],
                        pitch: 0,
                        style: 'light',
                        zoom: 4,
                        minZoom: 1,
                        maxZoom: 10
                    })
                });

                this.scene.on('loaded', () => {
                    this.layer = new CountryLayer(this.scene, {
                        data: this.ProvinceData,
                        joinBy: [ 'NAME_CHN', 'childStatistic' ],
                        depth: 1,
                        fill: {
                            color: 'rgba(201,203,204,0.36)'
                        },
                        bubble: {
                            enable: true,
                            size: {
                                field: 'totalConfirmed',
                                values: [10, 30]
                            },
                            color: {
                                field: 'totalConfirmed',  //颜色关联的字段,data中的value
                                values: (totalConfirmed) => {     //颜色的回调函数
                                    ['#ffde5d', '#ff6e40', '#ff0003','#BD0804', '#6A0400']
                                    return totalConfirmed < 100 ? '#ffde5d' :
                                        totalConfirmed < 1000 ? '#ff6e40' :
                                            totalConfirmed < 5000 ? '#ff0003' :
                                                totalConfirmed < 10000 ? '#cd0904':'#6A0400'
                                }
                            },
                            style:{
                                opacity:1
                            }
                        },
                        popup: {
                            enable: true,
                            Html: props => {
                                return `<span>${props.NAME_CHN}:</span><br/>
                                <span>确诊人数：${props.totalConfirmed}</span><br/>
                                <span>死亡人数：${props.totalDeath}</span><br/>
                                <span>治愈人数：${props.totalCured}</span>`;
                            }
                        }
                    });
                });

            })
        },
        methods:{
        }
    }


</script>

